.print {
  position: fixed;
  top: 1rem;
  right: 1rem;
  z-index: 30; }
  .print img {
    cursor: pointer;
    width: 2rem;
    height: auto;
    -webkit-box-shadow: 0rem 0rem 2px 1px #5a474780;
    box-shadow: 0rem 0rem 2px 1px #5a474780;
    border-radius: 50%;
    -webkit-transition: all .4s ease-out;
    -o-transition: all .4s ease-out;
    transition: all .4s ease-out; }
    .print img:hover {
      -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
      transform: scale(1.2); }

@media only screen and (max-width: 768px) {
  .print {
    display: none; } }



