/* Personal image */
.profile-card {
  color: white;
  height: 100%;
  background-color: #01375f; }
  @supports (background-image: linear-gradient(#01375f, #01375f26), url(../../images/coding.jpg)) {
    .profile-card {
      background-size: cover;
      background-position: top;
      background-image: -webkit-gradient(linear, left top, left bottom, from(#01375f), to(#01375f26)), url(../../images/coding.jpg);
      background-image: -webkit-linear-gradient(#01375f, #01375f26), url(../../images/coding.jpg);
      background-image: -o-linear-gradient(#01375f, #01375f26), url(../../images/coding.jpg);
      background-image: linear-gradient(#01375f, #01375f26), url(../../images/coding.jpg); } }

.profile-card__container {
  height: 9rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  -webkit-transition: -webkit-transform .5s background-image 2s;
  transition: -webkit-transform .5s background-image 2s;
  -o-transition: transform .5s background-image 2s;
  transition: transform .5s background-image 2s;
  transition: transform .5s background-image 2s, -webkit-transform .5s background-image 2s; }
  .profile-card__container:hover .profile-card__image {
    -webkit-box-shadow: 0px 0px 0px 12px rgba(0, 0, 0, 0.1);
    box-shadow: 0px 0px 0px 12px rgba(0, 0, 0, 0.1);
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
    background-image: url("../../images/flagArg.png"); }

.profile-card__image {
  border-radius: 50%;
  -webkit-transition: -webkit-box-shadow 0.3s ease;
  transition: -webkit-box-shadow 0.3s ease;
  -o-transition: box-shadow 0.3s ease;
  transition: box-shadow 0.3s ease;
  transition: box-shadow 0.3s ease, -webkit-box-shadow 0.3s ease;
  -webkit-box-shadow: 0px 0px 0px 8px rgba(0, 0, 0, 0.06);
  box-shadow: 0px 0px 0px 8px rgba(0, 0, 0, 0.06);
  background-image: url("../../images/foto-cv.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  border-radius: 3rem;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  -o-transition: transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  width: 9rem; }

.social-panel {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  font-size: 1.3em;
  margin-top: 3rem;
  margin-bottom: 1rem; }

.extract {
  margin-top: 3rem;
  border-top: 1px solid grey;
  border-radius: 12%;
  border-left: 1px solid grey;
  padding: 2rem; }

.personal-body-block {
  text-align: center;
  font-size: .8rem;
  margin: 3rem; }



